<template>
  <div>
    <mu-appbar style="width: 100%;" class="header backgroundNaviBar">
      <a @click="()=>{
          $router.go(-1)
        }">
        <mu-button icon slot="left" class="icon" size="20">
          <mu-icon value="keyboard_arrow_left"></mu-icon>
        </mu-button>
      </a>
      <span style="color:white" >{{$store.state.websocketWarningData[0].type}}</span>
    </mu-appbar>
    <div class="marginTop">
      <van-cell-group v-for="(item,index) in $store.state.websocketWarningData " :key="index">
        <!-- <van-cell title="单元格" value="内容" /> -->
        <van-cell
          :title="item.station"
          :value="item.device"
          :label="item.time"
          :title-style="red"
          size="large"
          class="borderBottom"
          @click="toHandle(item,item.id,index)"
        />
      </van-cell-group>
    </div>
  </div>
</template>
<script>
import { Dialog } from "vant";
import { Toast } from "vant";
export default {
  data() {
    return {
      red: "red"
    };
  },
  methods: {
    toHandle(content, id, index) {
      var user = sessionStorage.getItem("user");
      var _this = this;
      Dialog.alert({
        title: "预警内容",
        confirmButtonText: (user === "station" || user === "manager")?"确定":"去处理",
        message: content.type
      })
        .then(res => {
          // on confirm
          
          
          if (user === "station" || user === "manager") {
            // Toast("您没有处理权限！");
          } else {
            _this.$router.push({
              path: "TwoHandle",
              query: {
                id: id,
                index: index
              }
            });
          }
          console.log(id);
        });
        
    }
  }
};
</script>
<style scoped>
.borderBottom {
  border-bottom: 1px solid rgb(230, 228, 228);
}
.header {
  position: fixed;
  top: 0;
  width: 100%;
}
.marginTop {
  margin-top: 60px;
}
</style>


